import React from "react";
import Container from 'react-bootstrap/Container';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Image} from "react-bootstrap";
import '../../styles/AboutMe.css';




function AboutMe() {
    return (
        <div className={"aboutme-cont"}>
        <div>
            <h2 className={"about-title"}>About Me</h2>
        </div>
        <Container className={"cont-img"}>
            <Image className={"profileImg mx-auto"} src={"./images/myphoto.jpg"} fluid img />
            <p id={"p-aboutme"}>My name is Sarah Ghobj, I am a Mechatronics Engineer, currently doing my Masters in Sensors Systems Technology at Karlsruhe University of Applied Sciences.
                Since mid 2020, after taking a web development course within my study plan, I started developing interest in this field. I was always passionate about coding and always interested in learning new coding languages.
                I took an web development bootcamp on udemy with node.js to help me kick start in a field I am highly motivated ti build a professional career in.</p>
        </Container>

        </div>
    );
}

export default AboutMe;
